  return <><_components.p>{"¬†"}</_components.p>{"\n"}<_components.h1>{"üõ†Ô∏è Chain Indexer Troubleshooting"}</_components.h1>{"\n"}<_components.p>{"This guide covers common issues when running Yaci Store v1.0 as a standalone chain indexer (Spring Boot application) and their solutions."}</_components.p>{"\n"}<_components.h2 id={toc[0].id}>{toc[0].value}</_components.h2>{"\n"}<_components.h3 id={toc[1].id}>{toc[1].value}</_components.h3>{"\n"}<_components.p><_components.strong>{"Symptoms"}</_components.strong>{": The application starts, but block syncing doesn‚Äôt begin."}</_components.p>{"\n"}<_components.p><_components.strong>{"Solutions"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Ensure this configuration is enabled:"}</_components.li>{"\n"}</_components.ul>{"\n"}<pre><code class="language-properties"><_components.p>{"store.sync-auto-start=true"}</_components.p></code></pre>{"\n"}<_components.ul>{"\n"}<_components.li>{"Check connectivity to the node:"}{"\n"}<_components.ul>{"\n"}<_components.li>{"Is "}<_components.code>{"ogmios.url"}</_components.code>{" or "}<_components.code>{"n2c.socket-path"}</_components.code>{" correctly configured?"}</_components.li>{"\n"}<_components.li>{"Is the node fully synced and accepting connections?"}</_components.li>{"\n"}</_components.ul>{"\n"}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h3 id={toc[2].id}>{toc[2].value}</_components.h3>{"\n"}<_components.p><_components.strong>{"Symptoms"}</_components.strong>{": PostgreSQL database consumes excessive disk space."}</_components.p>{"\n"}<_components.p><_components.strong>{"Solutions"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Enable pruning in "}<_components.code>{"application.properties"}</_components.code>{":"}</_components.li>{"\n"}</_components.ul>{"\n"}<pre><code class="language-properties"><_components.p>{"store.utxo.pruning-enabled=true\nstore.transaction.pruning-enabled=true\nstore.utxo.pruning.interval=600"}</_components.p></code></pre>{"\n"}<_components.ul>{"\n"}<_components.li>{"Adjust pruning intervals as necessary."}</_components.li>{"\n"}<_components.li>{"Note: Pruning is currently supported for UTXO and transaction stores only."}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h3 id={toc[3].id}>{toc[3].value}</_components.h3>{"\n"}<_components.p><_components.strong>{"Symptoms"}</_components.strong>{": You expected governance or rewards data, but only block and transaction data appear."}</_components.p>{"\n"}<_components.p><_components.strong>{"Solutions"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Make sure all necessary stores are enabled:"}</_components.li>{"\n"}</_components.ul>{"\n"}<pre><code class="language-properties"><_components.p>{"store.reward.enabled=true\nstore.governance.enabled=true"}</_components.p></code></pre>{"\n"}<_components.ul>{"\n"}<_components.li>{"Check logs for initialization errors or misconfigurations."}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h3 id={toc[4].id}>{toc[4].value}</_components.h3>{"\n"}<_components.p><_components.strong>{"Symptoms"}</_components.strong>{": Rewards, DRep, or governance data differs from DB-Sync."}</_components.p>{"\n"}<_components.p><_components.strong>{"Solutions"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Refer to the "}<_components.a href="#%F0%9F%93%8B-ledger-state-mismatch-summary">{"Ledger State Mismatch Report"}</_components.a>{" if available."}</_components.li>{"\n"}<_components.li>{"Report discrepancies via GitHub Issues with clear details (epoch, address, tx hash, etc.)."}</_components.li>{"\n"}<_components.li>{"Ensure you‚Äôre running the latest Yaci Store version with all patches applied."}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h3 id={toc[5].id}>{toc[5].value}</_components.h3>{"\n"}<_components.p><_components.strong>{"Symptoms"}</_components.strong>{": Queries are slow or time out."}</_components.p>{"\n"}<_components.p><_components.strong>{"Solutions"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"For containerized deployments:"}{"\n"}<_components.ul>{"\n"}<_components.li>{"Use "}<_components.code>{"shm_size=2g"}</_components.code>{" for PostgreSQL in Docker Compose."}</_components.li>{"\n"}</_components.ul>{"\n"}</_components.li>{"\n"}<_components.li>{"For native installations:"}{"\n"}<_components.ul>{"\n"}<_components.li>{"Tune "}<_components.code>{"postgresql.conf"}</_components.code>{" parameters for production workloads:"}{"\n"}<_components.ul>{"\n"}<_components.li><_components.code>{"shared_buffers"}</_components.code></_components.li>{"\n"}<_components.li><_components.code>{"work_mem"}</_components.code></_components.li>{"\n"}<_components.li><_components.code>{"max_connections"}</_components.code></_components.li>{"\n"}</_components.ul>{"\n"}</_components.li>{"\n"}</_components.ul>{"\n"}</_components.li>{"\n"}<_components.li>{"Ensure indexes exist on frequently queried fields (e.g. block number, tx hash, address)."}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.blockquote>{"\n"}<_components.p>{"üí¨ "}<_components.strong>{"Still stuck?"}</_components.strong></_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Ask questions on "}<_components.a href="https://discord.gg/6AnKrFx4ne">{"Bloxbean Developers Discord"}</_components.a>{"& "}<_components.a href="https://github.com/bloxbean/yaci-store/discussions">{"GitHub Discussions"}</_components.a></_components.li>{"\n"}<_components.li>{"Report bugs via the "}<_components.a href="https://github.com/bloxbean/yaci-store/issues">{"Issue Tracker"}</_components.a></_components.li>{"\n"}</_components.ul>{"\n"}</_components.blockquote></>;
